@font-face {
    font-family: "MyFontRu";
    src: url("../../fonts/17634.ttf") format("truetype");
}
* {
    box-sizing: border-box;
}
body {
    display: block;
    padding: 0;
    margin: 0;
    overflow-x: hidden;
}
.container {
    padding: 0 15px;
    width: 100%;
    height: 100%;
}
header {
    height: 100px;
    width: 100%;
    color: white;
    text-align: center;
    background-color: rgb(44, 76, 255);
    padding: 15px;
}
h2 {
    font-family: "MyFontRu";
    font-size: 40px;
    margin: 0 auto;
}
h4 {
    font-family: "MyFontRu";
}
footer {
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    color: white;
    text-align: center;
    height: 60px;
    background-color: rgb(44, 76, 255);
    padding: 15px;
}
footer h4 {
    position: absolute;
    bottom: 0px;
    right: 25%;
    left: 25%;
    margin-left: -100px;
}
.identification {
    position: relative;
    transform: translate(-50%, -50%);
}

.MuiInputLabel {
    display: flex !important;
    flex-direction: row !important;
    justify-content: flex-end;
    align-items: center;
    animation: none !important;
}
.MuiInputLabel span {
    content: " *";
    color: red !important;
}
.MuiInputLabel .Muifocused {
    color: black !important;
}
.textFieldContainer {
    position: relative;
    width: 100%;
    margin: 20px 0;
    text-align: center;
    font-family: "MyFontRu";
    font-size: 40px;
    display: flex;
    flex-direction: column;
    height: 90%;
    overflow-y: scroll;
}

.textField::before,
.textField::after {
    content: "" !important;
    display: none !important;
}
.buttonContainer {
    border-bottom: 1px solid black;
    text-align: center;
    padding: 5px;
}
.textField{
    margin: 20px 5px;
    border:3px solid black;
}
